div.tfw-layout-Tabstrip {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
}

div.tfw-layout-Tabstrip>header {
    flex: 0 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: stretch;
    margin: 0 0 -1px;
    z-index: 1;
}

div.tfw-layout-Tabstrip>header>div.void {
    flex: 1 1 auto;
    border-bottom: 1px solid var(--thm-black-5);
}

div.tfw-layout-Tabstrip>header>button {
    cursor: pointer;
    color: var(--thm-bgP);
    font-variant: small-caps;
    font-weight: bold;
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    min-height: 2em;
    max-height: 2em;
    height: 2em;
    border-bottom: 1px solid var(--thm-black-5);
    margin: 0;
    padding: 0 .5em;
}

div.tfw-layout-Tabstrip>header>button:active,
div.tfw-layout-Tabstrip>header>button:hover {
    color: var(--thm-bgPL);
}

div.tfw-layout-Tabstrip>header>button.selected {
    border: 1px solid var(--thm-black-5);
    border-bottom: 1px solid var(--thm-bg2);
    z-index: 2;
    overflow: hidden;
    border-radius: .5rem .5rem 0 0;
}

div.tfw-layout-Tabstrip>div {
    position: relative;
    flex: 1 1 auto;
    margin: 0;
    padding: .5em;
    border: 1px solid var(--thm-black-5);
    border-top: none;
    z-index: 0;
}
