.tfw-view-Input {
    display: inline-flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
    margin: .5rem;
    transition: all .2s;
}

.tfw-view-Input.wide {
    display: flex;
}

.tfw-view-Input label {
    font-size: 80%;
    padding: 2px .25rem;
}

.tfw-view-Input > div.error {
    width: 100%;
    line-height: 1.2rem;
    font-size: 80%;
    font-weight: normal;
    padding: 0 .5rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    background: transparent;
    text-align: left;
}

.tfw-view-Input > div.input {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.tfw-view-Input > div.input > * {
    flex: 0 0 auto;
}

.tfw-view-Input > div.input > input {
    flex: 1 1 auto;
    width: 100%;
    font-size: inherit;
    font-family: inherit;
    border: none;
    padding: 0 .25rem;
    line-height: 2rem;
    transition: all .2s;
}

.tfw-view-Input.invalid input {
    position: relative;
    text-decoration-style: wavy;
    text-decoration-color: red;
    text-decoration-color: var(--thm-bgSD);
}

.tfw-view-Input > div.input > div.icon {
    display: flex;
    align-items: center;
    justify-content: center;
}
